<template>
  <div class="people-type">
    <div class="people-type-style">
      <separator></separator>
      <div class="type-content">
        <div class="top-box">
          <i class="blue"></i>
          <div class="title">
            <span>{{$t("personnelType.title")}}</span>
          </div>
          <div class="type-content-fix">
            <el-button type="primary" @click="addPeopleType">{{$t("personnelType.add")}}</el-button>
            <el-button type="primary" @click="fixPeopleField">{{$t("personnelType.field")}}</el-button>
          </div>
        </div>
        <div class="show-table">
           <el-table
              v-loading="tableBoxLoading"
              :data="showData"
              :header-cell-style="{background:'rgba(245,248,252,1)'}"
              height="631">
              <el-table-column
                :label="$t('allocation.index')"
                type="index"
                width="150"
                align="center">
              </el-table-column>
              <el-table-column
                prop="name"
                :label="$t('personnelType.title')"
                align="center">
              </el-table-column>
              <el-table-column
                :label="$t('common.operation')"
                align="center">
                <template slot-scope="scope">
                  <el-button
                    @click.native.prevent="editRow(scope)"
                    type="text"
                    size="small">
                    {{$t("common.edit")}}
                  </el-button>
                  <el-button
                    @click.native.prevent="deleteRow(scope.row)"
                    :disabled="filterName(scope.row)"
                    type="text"
                    size="small">
                    {{$t("common.delete")}}
                  </el-button>
                </template>
              </el-table-column>

            </el-table>
            <div class="pagination-box">
                <el-pagination
                  :current-page="currentPage"
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :page-sizes="[5,10,20]"
                  :page-size.sync="pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="total">
                </el-pagination>
              </div>
        </div>
      </div>
    </div>
    <!-- <new-people-type-dialog :isShow="true"></new-people-type-dialog> -->
    <el-dialog
      :title="dialogPeopleTitle"
      :visible.sync="dialogAddPeopleType"
      width="30%">
        <new-people-type-dialog :isEditPersonType="isEditPersonType"
                                :dialogAddPeopleType="dialogAddPeopleType"
                                :peopleTypeInfo="peopleTypeInfo"
                                :peopleBaseInfo="peopleBaseInfo"
                                @dialogClosePeopleType="dialogClosePeopleType"></new-people-type-dialog>
    </el-dialog>

     <el-dialog
      :title="$t('personnelType.field')"
      :visible.sync="dialogFixField"
      width="35%">
        <people-attr-config-dialog :dialogFixField="dialogFixField" @closeDialogFixField="closeDialogFixField"></people-attr-config-dialog>
    </el-dialog>
  </div>
</template>

<script>
import separator from "../../../public/separator.vue";
import newPeopleTypeDialog from "../../project/cp/people_manage/newPeopleTypeDialog";
import peopleAttrConfigDialog from "../../project/cp/people_manage/peopleAttrConfigDialog";
import APICONFIG from "../../../../config/api.js";

export default {
  name: 'peopleType',
  components: {
    separator,
    newPeopleTypeDialog,
    peopleAttrConfigDialog
  },
  data () {
    return {
      netApi: '',
      currentPage: 1,
      pageSize: 10,
      total: 6,
      tableData: [],
      showData: [],
      dialogPeopleTitle: '新增人员类型',
      dialogAddPeopleType: false,
      dialogFixField: false,
      tableBoxLoading: false,
      peopleTypeInfo: [],
      newTableData: [],
      peopleBaseInfo: {
        peopleTypeName: '', // 人员类型的名字
        peopleTypeNo: '', // 人员类型编号
        categoryValue: '' // 包含是否分组
      },
      isEditPersonType: false, // 是否编辑人员类型
      deleteform: {
        no: ''
      }
    }
  },
  watch: {
    currentPage(newval) {
      this.changeShowData()
    },
    pageSize() {
      this.changeShowData()
    }
  },
  created() {
    this.netAPI = APICONFIG().get("people_manage");
     //获取人员类型
    this.getPeopleCotegory()
  },
  methods: {
    changeShowData() {
      this.showData = this.tableData.slice((this.currentPage - 1) * this.pageSize, (this.currentPage - 1) * this.pageSize + this.pageSize)
    },
    handleSizeChange(pageSize) {
    },
    handleCurrentChange(Current) {
      this.currentPage = Current
    },
    filterName(row) {
      console.log(row)
      // 常客, 访客, 黑名单
      if(row.no == "100010001001" || row.no == "100010001002" || row.no == "100010001003") {
        return true
      }
    },
    editRow(val) {
      console.log(val)
      this.dialogAddPeopleType = true
      this.peopleTypeInfo = val.row.children
      this.peopleBaseInfo.id = val.row.id
      // 人员类型的名字
      this.peopleBaseInfo.peopleTypeName = val.row.name
      // 人员类型的编号
      this.peopleBaseInfo.categoryNo = val.row.no
      this.peopleBaseInfo.categoryValue = val.row.value
      // 处于编辑状态
      this.isEditPersonType = true
      this.dialogPeopleTitle = this.$t("personnelType.edit")
    },
    deleteRow(row) {
      this.deleteform.no = row.no;
      this.deleteform.name = row.name;
      this.$http.post(this.netAPI.typeRemove, [this.deleteform]).then(
        res => {
          if(res.body.status == 200) {
            this.initParam()
            this.$message.success(this.$t("delete.success"))
            this.getPeopleCotegory()
          } else {
            // this.$message.warning(`${res.body.message}`);
            // this.$message.warning('Delete failed. Maybe this person has a type of existing person.')
          }
        },
        err => {
          this.$message.warning(`${err}`);
        }
      )
    },
    // 初始化
    initParam() {
      this.currentPage = 1
    },
    getPeopleCotegory() {
      this.tableBoxLoading = true;
      this.$http.get(this.netAPI.findPeopleCategory).then(
      res => {
        if (res.body.status == 200) {
          this.total = res.data.data.length
          this.tableData = res.data.data
          this.changeShowData()
          this.tableBoxLoading = false;
        } else {
          this.$message.warning(`${this.$t('personnelType.getPersonFailed')}${res.body.message}`)
        }
      })
    },
    addPeopleType() {
      this.dialogAddPeopleType = true
      this.isEditPersonType = false
      this.peopleBaseInfo.peopleTypeName = ''
      this.dialogPeopleTitle = this.$t('personnelType.addPersonType')
    },
    fixPeopleField() {
      this.dialogFixField = true
    },
    dialogClosePeopleType() {
      // 关闭窗口
      this.dialogAddPeopleType = false
      this.getPeopleCotegory()
    },
    closeDialogFixField() {
      this.dialogFixField = false
    }
  }
}
</script>

<style lang="scss">
  .people-type {
    margin: 0;
    .people-type-style {
      // position: absolute;
      // top: 9px;
      // right: 0;
      // left: 0;
      // margin: auto;
      // background: #ffffff;
      // height: 92%;
      // width: 95%;
      // border-radius: 5px;
      // padding: 10px;
      .type-content {
        background:rgba(255,255,255,1);
        border:1px solid rgba(233,233,233,1);
        box-shadow:#D1D1D1 0 2px 4px 0;
        border-radius:5px;
        margin-top: 10px;
        .top-box{
          position: relative;
          box-sizing: border-box;
          padding: 10px 30px;
          .blue{
          width:12px;
          height:20px;
          background:rgba(67,131,220,1);
          border-radius:0px 3px 3px 0px;
          position: absolute;
          left: 0;
          top: 19px;
          }
          .title{
              height: 0.77rem;
              display: flex;
              align-items: center;
              font-size: 20px;
              font-weight: bolder;
          }
          .type-content-fix {
          text-align: right;
          }
        }
        .show-table{
          box-sizing: border-box;
          padding: 0 30px;
          .pagination-box{
              height: 50px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
        }
      }
    }
    height: 100%;
    vertical-align: top;
    position: relative;
  }
</style>



